      vm.$store.dispatch('loadDossierFormTemplates', filter).then(function (result) {
        if (result['newFormScript']) {
          console.log('vm.currentBooking 2', vm.currentBooking);
          vm.currentBooking.state = 4;
          vm.$store.dispatch('updateBooking', vm.currentBooking).then(function (result1) {}).catch(function (reject1) {});
          var urlRedirect = '/web/cuc-lanh-su/mot-cua-dien-tu#/danh-sach-ho-so/0/ho-so/0/NEW';
          var query = '?q=/o/rest/v2/dossiers/todo?order=true&step=110,500&service_config=' + vm.filterCreateDossier['serviceConfigId'] + '&template_no=' + vm.filterCreateDossier['dossierTemplateNo'] + '&eform=true&eformCode=' + vm.currentBooking['codeNumber']; // window.location.href = urlRedirect + query

          window.open(urlRedirect + query, '_blank');
        } else {
          vm.$store.dispatch('postDossier', vm.filterCreateDossier).then(function (result) {
            vm.currentBooking.state = 4;
            vm.$store.dispatch('updateBooking', vm.currentBooking).then(function (result1) {}).catch(function (reject1) {});
            var urlRedirect = '/web/cuc-lanh-su/mot-cua-dien-tu#/danh-sach-ho-so/0/ho-so/' + result.dossierId + '/NEW';
            var query = '?q=/o/rest/v2/dossiers/todo?order=true&step=110,500&service_config=' + vm.filterCreateDossier['serviceConfigId'] + '&template_no=' + vm.filterCreateDossier['dossierTemplateNo'] + '&eform=true'; // window.location.href = urlRedirect + query

            window.open(urlRedirect + query, '_blank');
          }).catch(function (reject) {});
        }
      });
    },
    getGateNumber: function getGateNumber() {
      var vm = this;
      vm.$store.dispatch('getGateNumber').then(function (result) {
        vm.currentGate = result;
        vm.getBookingCalling();
      }).catch(function (reject) {});
    },
    updateGateNumber: function updateGateNumber(val) {
      var vm = this;
      var filter = {
        gateNumber: val
      };
      vm.$store.dispatch('updateGateNumber', filter).then(function (result) {
        vm.getBookingCalling();
      });
    },
    getCurrentDate: function getCurrentDate() {
      var date = new Date();
      return "".concat(date.getDate().toString().padStart(2, '0'), "/").concat((date.getMonth() + 1).toString().padStart(2, '0'), "/").concat(date.getFullYear());
    },
    getStateName: function getStateName() {
      var vm = this;
      return vm.stateList.filter(function (state) {
        return state.stateCode === vm.stateSelected;
      })[0]['stateName'];
    },
    getColor: function getColor(level) {
      if (level === 2) {
        return 'green';
      } else if (level === 3) {
        return 'orange darken-1';
      } else if (level === 4) {
        return 'red';
      }
    },
    getFieldPick: function getFieldPick() {
      var vm = this;
      var classPK = 'BOOKING';
      vm.$store.dispatch('getFieldPick', classPK).then(function (result) {
        try {
          var formData = result;
          vm.serviceInfoSelected = formData.serviceCode;
          vm.serviceGroupSelected = vm.serviceGroupList.find(function (e) {
            return e.groupCode === formData.groupCode;
          });
          vm.filterBooking();
        } catch (err) {}
      }).catch(function (reject) {});
    },
    postFieldPick: function postFieldPick(key, val) {
      var vm = this;
      var filter = {
        classPK: 'BOOKING',
        key: key,
        val: val
      };
      vm.$store.dispatch('postFieldPick', filter).then(function (result) {}).catch(function (reject) {});
    }
  }
};